    :
    :2019-11-25
-->
<template>
    <div class="">
        <div class="title_div"></div>
        <!-- form -->
        <div class="form_div">
            <div class="form_title">   
                <i class="el-icon-document"></i>
                
            </div>
            <el-form :inline="true" 
                     :model="formData"
                     :rules="rules" 
                      ref="formData1"
                     :label-width="labelwidth"
                     class="demo-form-inline ">
                <el-form-item label="" prop="name" class="form_left">
                    <el-input v-model="formData.name" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="jobnum" class="form_left">
                    <el-input v-model="formData.jobnum" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" prop="department" class="form_left">
                    <el-input v-model="formData.department" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <el-form :inline="true" 
                     :model="formData"
                     :label-width="labelwidth"  
                     class="demo-form-inline">
                <el-form-item label="" class="form_left">
                    <el-input v-model="formData.position" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" class="form_left">
                    <el-date-picker
                        v-model="formData.Entry"
                        type="date"
                        placeholder="">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="" class="form_left">
                    <el-date-picker
                        v-model="formData.formal"
                        type="date"
                        placeholder="">
                    </el-date-picker>
                </el-form-item>
            </el-form>
             <el-form :inline="true" 
                     :model="formData"
                     :label-width="labelwidth"  
                     class="demo-form-inline">
                <el-form-item label="" class="form_left">
                    <el-input v-model="formData.phone" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" class="form_left">
                    <el-input v-model="formData.mail" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" class="form_left">
                    <el-select v-model="formData.contract" placeholder="">
                        <el-option
                            v-for="item in contractData"
                            :key="item.value"
                            :label="item.label"
                            :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>
            </el-form>
            <el-form :inline="true" 
                     :model="formData"
                     :label-width="labelwidth"  
                     class="demo-form-inline">
                <el-form-item label="" class="form_left">
                    <el-date-picker
                        v-model="formData.birthday"
                        type="date"
                        placeholder="">
                    </el-date-picker>
                </el-form-item>
                <el-form-item label="" class="form_left">
                    <el-input v-model="formData.nativeplace" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" class="form_left">
                    <el-input v-model="formData.shoole" placeholder=""></el-input>
                </el-form-item>
            </el-form>
            <el-form :inline="true" 
                     :model="formData"
                     :label-width="labelwidth"  
                     class="demo-form-inline">
                <el-form-item label="" class="form_left">
                    <el-input v-model="formData.education" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" class="form_left">
                    <el-input v-model="formData.major" placeholder=""></el-input>
                </el-form-item>
                <el-form-item label="" class="form_left">
                    <el-input v-model="formData.contacts" placeholder=""></el-input>
                </el-form-item>
            </el-form>

            <!--  -->
            <div class="wages_form">
                <div class="wages_title_div">
                    <div class="wages_title">
                        <i class="el-icon-tickets"></i>
                        
                    </div>
                    <div class="wages_hr">
                        <div></div>
                    </div>
                </div>

                <el-form :inline="true" 
                     :model="formData" 
                     :rules="rules"
                     ref="formData2"   
                     class="demo-form-inline">
                    <el-form-item label="" prop="cardnum">
                        <el-input v-model="formData.cardnum" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="formData.testsalary" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="">
                        <el-input v-model="formData.formalsalary" placeholder=""></el-input>
                    </el-form-item>
                </el-form>

                <el-form :inline="true" 
                     :model="formData" 
                     :rules="rules"
                     ref="formData3"  
                     class="demo-form-inline">
                    <el-form-item label="" prop="basic">
                        <el-input v-model="formData.basic" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="postwages">
                        <el-input v-model="formData.postwages" placeholder=""></el-input>
                    </el-form-item>
                    <el-form-item label="" prop="merits">
                        <el-input v-model="formData.merits" placeholder=""></el-input>
                    </el-form-item>
                </el-form>
                <!--  -->
                <div class="button_div">
                     <el-button type="primary" 
                                round
                                class="button"
                                @click="submitSub('formData1','formData2','formData3')" ></el-button>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    components:{},
    props:{},
    data(){
        return {
            formData:{
                user:"",
                jobnum:"",
                department:"",
                position:"",
                Entry:"",
                formal:"",
                phone:"",
                mail:"",
                contract:"",
                birthday:"",
                nativeplace:"",
                shoole:"",
                education:"",
                major:"",
                contacts:"",
                cardnum:"",
                testsalary:"",
                formalsalary:"",
                basic:"",
                postwages:"",
                merits:"",
            },
            rules:{
                name:[
                    { required: true, message: '', trigger: 'blur' },
                ],
                jobnum:[
                    { required: true, message: '', trigger: 'blur' },
                ],
                department:[
                    { required: true, message: '', trigger: 'blur' },
                ],
                cardnum:[
                    { required: true, message: '', trigger: 'blur' }
                ],
                basic:[
                    { required: true, message: '', trigger: 'blur' }
                ],
                postwages:[
                    { required: true, message: '', trigger: 'blur' }
                ],
                merits:[
                    { required: true, message: '', trigger: 'blur' }
                ],
            },
            //
            contractData:[
                {
                    value:"1",
                    label:''
                },
                {
                    value:"2",
                    label:''
                }
            ],

            //
            labelwidth:"100px"
        }
    },
    watch:{},
    computed:{},
    created(){},
    mounted(){},
    methods:{
        submitSub(formData1,formData2,formData3){
            this.$refs[formData1].validate((valid1) => {
                if (valid1) {
                    this.$refs[formData2].validate((valid2)=>{
                        if(valid2){
                            this.$refs[formData3].validate((valid3)=>{
                                if(valid3){
                                    alterFun('success','')
                                }else{
                                    this.alterFun('error','')
                                    return false;
                                }
                            })
                        }else{
                            this.alterFun('error','')
                            return false;
                        }
                    })
                } else {
                    this.alterFun('error','')
                    return false;
                }
            });
        },

        /** */
        alterFun(state,text){
            this.$message({
                message: text,
                type: state //success/info/warning/error
            });
        }
    }
    
}
</script>
<style scoped>
.title_div{
    text-align: left;
    font-size: 18px;
    margin: 20px;
}
.form_div{
    padding: 20px;
    background: #fff;
    box-shadow: 0 2px 12px 0 rgba(0, 0, 0, 0.1)
}
.form_title{
    text-align: left;
    color: #0062ef;
    font-size: 18px;
    margin-bottom: 10px;
}
.wages_title_div{
    display: flex;
}
.wages_title{
    text-align: left;
    color: #0062ef;
    font-size: 18px;
    width: 110px;
}
.wages_hr{
    height: 24px;
    width: 100%;
    display: flex;
    align-items: center
}
.wages_hr>div{
    height: 2px;
    width: 100%;
    background: #ccc;
}
.button_div{
    margin-top: 10px;
}
.demo-form-inline{
    display: flex;
    justify-content: space-around;  
}
.form_left{
    text-align: left;
}
.el-form-item{
    width: 320px;
}
.button{
    width: 23%;
    box-shadow: 0 0 5px #0062ef
}


</style>


